body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* FLEX */
.flex { display: flex; }

.horizontal, .horizontal-only { flex-direction: row; }
.vertical { flex-direction: column; }
@media screen and (max-width: 800px){
  .horizontal { flex-direction: column; }
}

.flex-1 { flex: 1 1; }
.flex-2 { flex: 2 1; }
.flex-3 { flex: 3 1; }
.flex-4 { flex: 4 1; }
.flex-5 { flex: 5 1; }
.flex-6 { flex: 6 1; }

.flex-center {
  justify-content: center;
  align-items: center;
}
.flex-left {
  justify-content: flex-start;
  align-items: center;
}
.flex-right {
  justify-content: flex-end;
  align-items: center;
}
@media screen and (max-width: 800px){
  .flex-left {
    justify-content: center;
    align-items: center;
  }
  .flex-right {
    justify-content: center;
    align-items: center;
  }
}

.text-center {
  text-align: center;
}

/* COMPONENTS */
.Header, .Contato {
  background-color: #5B130B;
  color: #fefefe;
}
.Header > img {
  width: 90%;
  max-width: 400px;
  margin: 50px 20px;
}

.img-pic {
  width: 80%;  
  margin: 5px;
  border: 1px solid #5B130B;
  border-radius: 10px;
}

.Contato {
  padding: 30px 0;
}

.Footer {
  padding: 10px 0;
  background-color: rgb(32, 32, 32);
  color: #fefefe;
}
